* {
  box-sizing: border-box;
}

html, body, #root {
  padding: 0;
  margin: 0;
  height: 100%;
}


.card {
  background: #fff;
  border-radius: 2px;
  height: 200px;
  margin: 10px;
  position: relative;
  width: 200px;
  box-shadow: 0 3px 6px #999, 0 3px 6px #999;
  align-content: center;
  text-align: center;
}

.card > .img-container {
  height: 100%;
  overflow: hidden;
  text-align: center;
  background: lightgrey;
}
.card > .img-container > img {
  padding: 5px;
  width: 100%;
  height: 100%;
}

.card:hover {
  color: white;
}



.wrapper {
  background: #fffefe;
  height: 100%;
  display: flex;
  flex-flow: row wrap;
  justify-content: space-around;
  align-content: flex-start;
  overflow: auto;
}

.nav {
  width: 100%;
  height: auto;
  font-size: 16px;
  padding: 20px;
}

li {
  display: inline-block;
  font-size: 20px;
  padding: 20px;
}

.title, .score, .high-score {
  font-weight: bold;
  text-align: center;
  font-family: 'Philosopher', sans-serif;
  font-size: 24px;
}

.background {
  background-color: #026635;
  box-shadow: 0 4px 8px rgb(180, 180, 180), 0 4px 8px rgb(180, 180, 180);
  margin-bottom: 12px;
}
.modal-body {
  font-size: 20px;
}

.modal-title {
  text-align: center !important;
  font-weight: bolder;
  font-size: 30px;
}

h2 {
  font-family: 'Source Sans Pro', sans-serif;
}

.content {
  margin-left: 100px;
  margin-right: 100px;
  margin-bottom: 50px;
  align-content: center;
}
